<template>
  <div class="login text-center ">
    <div class="bg-primary-gradient">
      <div class="logo">
        <v-img
            :src="require('../../assets/logo-white.png')"
            contain
            height="176"
            class="mb-5"
        />
      </div>

      <div class="call-to-action" style="margin: 1rem">
        <div class="text">
          <div>Tipo de consulta : {{ service.name }}</div>
          <div>Costo del servicio : S/. {{ service.price_final }}</div>
          <div>Código de solicitud: {{ consultationId | padleft }}</div>
        </div>
      </div>

      <div class="call-to-action-white" style="margin: 1rem; position: relative">
        <div class="text">
          ¡Tú pago ha sido realizado con éxito!
        </div>
        <img :src="require('../../assets/icons/lawyer-07.svg')" style="position: absolute; right: 0; bottom: 0">
      </div>

      <div style="width: 60%; margin: 0 auto">
        <v-img
            :src="require('../../assets/confirm.svg')"
        />
      </div>

      <div class="my-5">
        <v-btn width="240" @click="goToStart">Iniciar consulta</v-btn>
      </div>
      <div>
        <v-btn @click="goToRequests" width="240">Consultar más tarde</v-btn>
      </div>
    </div>
    <svg width="100%" height="124" viewBox="0 0 100 100" preserveAspectRatio="none">
      <defs>
        <polygon id="r1" points="0,0 100,0 70.8,100 0,22.5"/>
        <linearGradient id="g1" x1="0%" y1="0%" x2="100%" y2="0%">
          <stop stop-color="#0084CA" offset="0%"/>
          <stop stop-color="#00406C" offset="100%"/>
        </linearGradient>
      </defs>
      <use x="0" y="0" xlink:href="#r1" fill="url(#g1)"/>
    </svg>
    <div v-if="hasError" style="position: absolute; bottom: 0; text-align: left; width: 100%; padding: 0.5rem">
      <v-alert v-model="hasError" type="error" dismissible width="100%" transition="scale-transition">
        Correo y/o contraseña incorrectos.
      </v-alert>
    </div>
  </div>
</template>
<script>
import {mapActions, mapGetters} from 'vuex'

export default {
  name: 'MyLawyerPaymentConfirm',
  data: () => ({
    isLoading: false,
    hasError: false,
    consultationId: '0000000000',
  }),
  created() {
    if (this.service === null) {
      let serviceId = parseInt(this.$route.params.id)
      let service = this.services.find(obj => obj.id === serviceId)
      this.selectService(service)
    }
  },
  computed: {
    ...mapGetters(['user', 'services', 'service'])
  },
  mounted() {
    this.consultationId = this.$route.params.operationId
  },
  methods: {
    ...mapActions(['selectService']),
    goToStart() {
      this.$router.push({name: 'RequestStart', params: {id: this.consultationId}})
    },
    goToRequests() {
      this.$router.push({name: 'RequestIndex'})
    }
  }
}
</script>